.container {
  width: 100%;
  max-width: 1240px + 2 * $padding-desktop;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 $padding-desktop;

  @include vp-1199 {
    padding: 0 $padding-tablet;

    &--slider {
      padding: 0;
    }
  }

  @include vp-767 {
    padding: 0 $padding-mobile;
  }

  &--news {
    @include vp-767 {
      padding: 0;
    }
  }
}
